:root {
  --swiper-pagination-bullet-horizontal-gap: 0;
}

.hero {
  margin-top: -114px;

  @include vp-767 {
    margin-top: -94px;
  }
}

.hero__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.hero__item {
  display: flex;
  height: auto;
}

.hero__item--volunteering {
  & .hero__content {
    padding: 400px 5px 65px;

    @include vp-1199 {
      padding: 676px 45px 59px;
    }

    @include vp-767 {
      padding: 233px 15px 20px;
    }
  }

  & .hero__container {
    padding: 0 50px 31px;

    @include vp-1199 {
      margin: -4px 87px 0 0;
      padding: 0 40px 32px;
    }

    @include vp-767 {
      margin: -5px 0 0;
      padding: 0;
    }
  }

  & .hero__container p {
    padding-top: 10px;
  }
}

.hero__item--internship {
  & .hero__content {
    padding: 400px 5px 60px;

    @include vp-1199 {
      padding: 676px 45px 59px;
    }

    @include vp-767 {
      padding: 233px 15px 20px;
    }
  }

  & .hero__container {
    padding: 0 50px 40px;

    @include vp-1199 {
      margin: -4px 87px 0 0;
      padding: 0 40px 32px;
    }

    @include vp-767 {
      margin: -5px 0 0;
      padding: 0;
    }
  }

  & .hero__container p {
    padding-top: 5px;
  }
}


.hero__content {
  padding: 409px 5px 60px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  position: relative;

  @include vp-1199 {
    padding: 676px 45px 59px;
  }

  @include vp-767 {
    padding: 233px 15px 20px;
  }
}

.hero__pagination {
  position: relative;
  bottom: 0;
  display: flex;
  width: auto;
  padding: 14px 20px;
  background-color: $color-default-white;
  border-radius: 20px 20px 0 0;
  gap: 0 12px;

  &::after {
    width: 26px;
    height: 26px;
    position: absolute;
    content: "";
    top: 11px;
    right: -25px;
    background-image: url("../img/svg/angle.svg");
    transform: rotate(-90deg);
  }

  @include vp-1199 {
    padding: 13px 20px 16px;
    gap: 12px;

    &::after {
      top: 12px;
    }
  }

  @include vp-767 {
    padding: 9px 16px;
    border-radius: 12px 12px 0 0;
    gap: 9px;

    &::after {
      width: 10px;
      height: 10px;
      position: absolute;
      content: "";
      top: 19px;
      right: -10px;
      background-image: url("../img/svg/angle-small.svg");
      transform: rotate(-90deg);
    }
  }
}

.swiper .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: inherit;
  border: 1px solid $color-cerulean-blue;
  margin: 0;
  opacity: 1;

  &:not(:disabled):hover {
    @include hover;
    border: 1px solid $color-cerulean-blue;
    background-color: transparent;
  }

  &:not(:disabled):focus {
    @include focus;
    border: 1px solid $color-cerulean-blue;
  }

  &:not(:disabled):active {
    @include active;
    background-color: $color-cerulean-blue;
    border-color: $color-cerulean-blue;
  }

  &:focus-visible {
    outline: none;
  }

  &:disabled {
    opacity: 10%;
    cursor: default;
  }
}

.swiper .swiper-pagination-bullet-active {
  background-color: $color-cerulean-blue;
  border-color: $color-cerulean-blue;
}

.hero__content h2 {
  max-width: calc(78% + 1px);
  margin: 0;
  padding: 40px 50px 33px;
  font-size: 54px;
  line-height: 62px;
  font-weight: 400px;
  color: $color-cerulean-blue;
  background-color: $color-default-white;
  border-radius: 0 30px 30px 0;

  @include vp-1199 {
    max-width: calc(97% + 1px);
    margin: 0;
    padding: 31px 40px 23px;
    font-size: 36px;
    line-height: 44px;
  }

  @include vp-767 {
    max-width: 100%;
    margin: 0;
    padding: 20px 14px 16px;
    font-size: 22px;
    line-height: 26px;
    border-radius: 0 16px 16px 0;
  }
}

.hero__container {
  position: relative;
  display: flex;
  align-items: center;
  margin: -7px 468px 0 0;
  padding: 0 50px 31px;
  background-color: $color-default-white;
  border-radius: 0 0 30px 30px;
  gap: 0 30px;

  &::after {
    width: 26px;
    height: 26px;
    position: absolute;
    content: "";
    top: 6px;
    right: -25px;
    background-image: url("../img/svg/angle.svg");
  }

  @include vp-1199 {
    margin: -4px 87px 0 0;
    padding: 0 40px 32px;
    align-items: flex-start;

    &::after {
      top: 4px;
    }
  }

  @include vp-767 {
    flex-direction: column;
    margin: -5px 0 0;
    padding: 0;
    background-color: transparent;

    &::after {
      display: none;
    }

    & div {
      width: 182px;
      margin-top: -1px;
      padding: 10px 15px;
      position: relative;
      background-color: $color-default-white;
      border-radius: 0 0 14px 14px;

      &::before {
        width: 26px;
        height: 26px;
        position: absolute;
        content: "";
        top: 0;
        right: -25px;
        background-image: url("../img/svg/angle.svg");
      }

      @include vp-767 {
        position: relative;
        max-width: 63%;
        margin: 0;
        padding: 10px 15px;
        background-color: $color-default-white;
        border-radius: 0 0 14px 14px;

        &::before {
          width: 10px;
          height: 10px;
          position: absolute;
          content: "";
          top: 0;
          right: -10px;
          background-image: url("../img/svg/angle-small.svg");
        }
      }
    }
  }
}

.hero__container p {
  width: 446px;
  margin: 0;

  @include vp-1199 {
    width: 100%;
    line-height: 20px;
  }

  @include vp-767 {
    position: relative;
    max-width: 90%;
    padding: 0 14px 10px;
    font-size: 14px;
    background-color: $color-default-white;
    border-radius: 0 0 14px 0;

    &::after {
      width: 10px;
      height: 10px;
      position: absolute;
      content: "";
      top: 5px;
      right: -9px;
      background-image: url("../img/svg/angle-small.svg");
    }
  }
}

.hero__container a {
  align-self: center;
  margin: -1px 0 0;

  &::after {
    width: 46px;
    height: 46px;
  }

  & svg {
    width: 10px;
    height: 10px;
    right: 21px;

    @include vp-1199 {
      right: 16px;
    }
  }

  @include vp-1199 {
    align-self: flex-start;
    margin: 4px 0 0;

    & svg {
      right: 21px;
    }
  }

  @include vp-767 {
    position: relative;
    margin: 0;
    background-color: $color-default-white;

    &::after {
      width: 34px;
      height: 34px;
    }

    & svg {
      width: 8px;
      height: 8px;
      right: 16px;
    }
  }
}

.hero__item picture {
  position: absolute;
  z-index: -1;
  display: flex;
  width: 100%;
  height: 100%;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: $overlay-photo;
    inset: 0;
  }

  img {
    width: 100%;
    object-fit: cover;
  }
}
