.participation {
  background-color: $color-cerulean-blue;
}

.participation__wrapper {
  max-width: 1240px;
  padding: 125px 0 108px;
  margin: 0 auto;
  display: flex;
  gap: 138px;

  @include vp-1199 {
    max-width: 100%;
    padding: 98px 0 99px;
    flex-direction: column;
    gap: 42px;
  }

  @include vp-767 {
    padding: 67px 0;
    gap: 33px;
  }
}

.participation__title {
  max-width: 498px;
  display: flex;
  flex-direction: column;
  gap: 35px;

  @include vp-1199 {
    max-width: 100%;
    gap: 35px;
  }

  @include vp-767 {
    gap: 22.5px;
  }
}

.participation__title h2 {
  width: 100%;
  margin: 0 0 -4px 0;
  font-size: 50px;
  line-height: 50px;
  font-weight: 400;
  color: $color-default-white;

  @include vp-1199 {
    font-size: 34px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }
}

.participation__title p {
  margin: 0;
  font-weight: 400;
  color: $color-default-white;

  @include vp-1199 {
    margin-top: -2px;
    max-width: 498px;
    line-height: 20px;
  }

  @include vp-767 {
    max-width: 100%;
    font-size: 14px;
  }
}

.participation__image {
  width: 100%;
  min-height: 363px;
  border-radius: 16px;
  background-color: $color-cerulean-blue;

  @include vp-1199 {
    min-height: 375px;
  }

  @include vp-767 {
    min-height: 200px;
  }
}

.participation__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;

  @include vp-767 {
    min-height: 200px;
  }
}

.participation__conditions {
  max-width: 498px;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  gap: 55px;

  @include vp-1199 {
    max-width: 100%;
    margin-right: 0;
    gap: 42px;
  }

  @include vp-767 {
    gap: 25px;
  }
}

.participation__conditions h3 {
  margin: 3px 0 0 0;
  font-size: 32px;
  line-height: 36px;
  font-weight: 400;
  color: $color-default-white;

  @include vp-1199 {
    font-size: 28px;
    line-height: 28px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 22px;
  }
}

.participation__conditions ul {
  margin: 0;
  padding: 0 0 0 20px;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 26px;

  @include vp-1199 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 48px;
  }

  @include vp-767 {
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
}

.participation__conditions li {
  max-width: 478px;
  padding: 22px 25px;
  position: relative;
  display: flex;
  align-items: center;
  border: 1px solid $color-default-white;
  border-radius: 16px;
  color: $color-default-white;

  &::before {
    position: absolute;
    content: attr(data-number);
    width: 42px;
    height: 42px;
    background-color: $color-cerulean-blue;
    top: -17px;
    left: -21px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    line-height: 22px;
    font-weight: 500;
    color: $color-default-white;
  }

  @include vp-1199 {
    max-width: 100%;
    min-width: 305px;
    padding: 25px;
    align-items: flex-start;
    line-height: 20px;
  }

  @include vp-767 {
    max-width: 100%;
    min-width: 100%;
    padding: 18px 25px;
    align-items: flex-start;
    font-size: 14px;

    &::before {
      width: 32px;
      height: 32px;
      top: -5px;
      left: -16px;
      font-size: 18px;
      line-height: 18px;
    }
  }
}
