.about__wrapper {
  padding: 118px 0 120px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 60px;

  @include vp-1199 {
    padding: 95px 0 97px;
    max-width: 100%;
    margin: 0;
  }

  @include vp-767 {
    padding: 66px 0 33px;
    gap: 40px;
  }
}

.about__text {
  max-width: 1134px;
  display: flex;
  justify-content: space-between;

  @include vp-1199 {
    max-width: 498px;
    gap: 30px;
    flex-direction: column;
  }

  @include vp-767 {
    max-width: 100%;
  }
}

.about__title {
  display: flex;
  flex-direction: column;
  gap: 30px;
  max-width: 477px;

  @include vp-1199 {
    max-width: 100%;
    gap: 24px;
  }

  @include vp-767 {
    max-width: 100%;
    gap: 11px;
  }
}

.about__title h2 {
  margin: 0;
  padding-top: 5px;
  width: 100%;
  font-size: 50px;
  line-height: 50px;
  font-weight: 400;
  color: $color-cerulean-blue;

  @include vp-1199 {
    font-size: 34px;
    line-height: 50px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }
}

.about__title p {
  margin: 0;
  width: 100%;
  font-size: 22px;
  line-height: 32px;
  color: $color-charcoal;

  @include vp-1199 {
    font-size: 20px;
    line-height: 26px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 22px;
  }
}

.about__description {
  display: flex;
  flex-direction: column;
  gap: 33px;
  max-width: 498px;

  @include vp-1199 {
    gap: 22px;
    max-width: 100%;
  }

  @include vp-767 {
    gap: 10px;
  }
}

.about__description h3 {
  margin: 0;
  font-size: 32px;
  line-height: 36px;
  font-weight: 400;
  color: $color-cerulean-blue;

  @include vp-1199 {
    font-size: 28px;
    line-height: 28px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 22px;
  }
}

.about__description p {
  margin: 0;
  line-height: 26px;
  color: $color-charcoal;

  @include vp-1199 {
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.about__button {
  max-width: 277px;
  letter-spacing: 0;
  font-size: 14px;
  line-height: 14px;
  background-color: transparent;

  @include vp-1199 {
    padding-left: 30px;
    margin-top: 14px;
    letter-spacing: 0.28px;
  }

  @include vp-767 {
    max-width: 221px;
    margin-top: 6px;
    padding-left: 20px;
    font-size: 12px;
    line-height: 12px;
  }

  @include focus {
    outline: none;
  }
}

.about__button:focus-visible:not([disabled]) {
  background-color: $color-cerulean-blue;
  color: $color-default-white;
}

.about__button:focus-visible:not([disabled])::after {
  background-color: $color-default-white;
}

.about__button:focus-visible:not([disabled]) svg {
  color: $color-cerulean-blue;
  transform: rotate(45deg);
}

.about__data {
  width: 100%;
  background-color: $color-cerulean-blue;
  border-radius: 20px;
  padding: 92px 105px 56px 125px;

  @include vp-1199 {
    padding: 62px 60px 40px;
  }

  @include vp-767 {
    padding: 42px 20px 40px 30px;
  }
}

.about__data ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 50px;

  @include vp-1199 {
    column-gap: 50px;
    row-gap: 42px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 22px;
  }
}

.about__data-item {
  max-width: 478px;
  padding: 30px 40px 30px 60px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid $color-default-white;
  border-radius: 16px;
  gap: 0 10px;

  &::before {
    position: absolute;
    content: "";
    width: 42px;
    height: 42px;
    background-color: $color-cerulean-blue;
    background-image: url("../img/svg/star.svg");
    color: $color-default-white;
    background-size: 22px 22px;
    background-repeat: no-repeat;
    background-position: center;
    top: -21px;
    left: -21px;
  }

  @include vp-1199 {
    padding: 30px 40px 22px 30px;
    max-width: 100%;
    min-width: 264px;
    min-height: 168px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 7px;

    &::before {
      top: -23px;
    }
  }

  @include vp-767 {
    max-width: 100%;
    min-width: 100%;
    min-height: 128px;
    padding: 18px 17px;
    gap: 4px;
  }
}

.about__data-item h4 {
  margin: 0;
  max-width: 171px;
  font-size: 26px;
  line-height: 30px;
  font-weight: 400;
  color: $color-default-white;
  text-transform: uppercase;
  letter-spacing: -0.56px;

  @include vp-1199 {
    max-width: 61%;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.2px;
  }

  @include vp-767 {
    max-width: 100%;
    font-size: 16px;
    line-height: 20px;
  }
}

.about__data-item p {
  margin: 0;
  max-width: 190px;
  line-height: 20px;
  color: $color-alabaster;
  letter-spacing: -0.5px;

  @include vp-1199 {
    max-width: 100%;
    line-height: 20px;
  }

  @include vp-767 {
    max-width: 100%;
    padding-right: 3px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 300;
  }
}
