.contacts {
  background-color: $color-cerulean-blue;
}

.contacts__wrapper {
  max-width: 1240px;
  margin: 0 auto;
  padding: 120px 0;
  display: flex;
  flex-direction: column;
  gap: 40px;

  @include vp-1199 {
    max-width: 100%;
    margin: 0;
    padding: 115px 0 100px;
    gap: 31px;
  }

  @include vp-767 {
    padding: 85px 0 66px;
  }
}

.contacts__text {
  padding-bottom: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid rgb(255, 255, 255, 0.2);

  @include vp-1199 {
    padding-bottom: 25px;
    flex-direction: column;
    justify-content: unset;
    align-items: flex-start;
    gap: 27px;
  }

  @include vp-767 {
    padding-bottom: 22px;
    gap: 28px;
  }
}

.contacts__text h2 {
  margin: -15px 0 0 0;
  font-size: 50px;
  line-height: 50px;
  font-weight: 400;
  color: $color-default-white;

  @include vp-1199 {
    width: 100%;
    font-size: 34px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }
}

.contacts__address {
  display: flex;
  list-style: none;
  max-width: 70%;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;

  @include vp-1199 {
    width: 100%;
    max-width: 100%;
    justify-content: space-between;
    gap: 0;
  }

  @include vp-767 {
    flex-direction: column;
    justify-content: flex-start;
    gap: 12px;
  }
}

.contacts__address li {
  display: flex;
  flex-direction: column;
  gap: 17px;

  @include vp-1199 {
    gap: 19px;
  }

  @include vp-767 {
    gap: 10px;
  }
}

.contacts__address span {
  font-size: 12px;
  line-height: 12px;
  color: rgba(240, 239, 233, 0.8);
  text-transform: uppercase;
}

.contacts__address p {
  margin: 0;
  color: $color-default-white;

  @include vp-1199 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 14px;
  }
}

.contacts__link {
  font-size: 16px;
  line-height: 26px;
  color: $color-default-white;
  text-transform: none;

  @include vp-767 {
    width: fit-content;
    font-size: 14px;
    line-height: 20px;
    border-bottom: 1px solid transparent;
  }
}

.contacts__link:hover,
.contacts__link:focus {
  @include hover;
  border-bottom: 1px solid $color-default-white;
}

.contacts__link:active {
  @include hover;
  color: $color-transparent;
}

.contacts__location {
  max-width: 290px;
  margin-right: 28px;
}

.contacts__phone {
  max-width: 140px;
  margin-right: 55px;

  @include vp-1199 {
    margin-right: 52px;
  }
}

.contacts__email {
  max-width: 110px;

  @include vp-1199 {
    gap: 15px;
  }
}

.contacts__container {
  border-radius: 16px;
  max-width: 100%;
  min-height: 500px;

  @include vp-1199 {
    max-width: 100%;
    min-height: 500px;
  }

  @include vp-767 {
    min-height: 240px;
  }
}

.contacts__map img {
  min-height: 100%;
  object-fit: cover;
}
