.reviews {
  padding: 60px 0 115px;
  display: flex;
  flex-direction: column;
  gap: 29px;

  @include vp-1199 {
    padding: 47px 0 111px;
    gap: 25px;
  }

  @include vp-767 {
    position: relative;
    padding: 37px 15px 26px;
    gap: 14px;
  }
}

.reviews__title {
  width: 100%;
  max-width: 1270px;
  margin: 0;
  padding: 0 18px;
  font-size: 50px;
  line-height: 50px;
  font-weight: 400;
  color: $color-cerulean-blue;
  text-align: start;
  align-self: center;

  @include vp-1199 {
    max-width: 100%;
    padding: 0 45px;
    font-size: 34px;
  }

  @include vp-767 {
    padding: 0;
    font-size: 20px;
    line-height: 20px;
  }
}

.reviews__slider {
  width: 100%;
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;

  @include vp-1199 {
    max-width: 100%;
    margin: 0;
    padding: 0 0 0 45px;
  }

  @include vp-767 {
    padding: 0;
  }
}

.reviews__list {
  margin-bottom: 5px;
  padding: 0;
  list-style: none;

  @include vp-1199 {
    margin-bottom: 24px;
  }
}

.reviews__item {
  max-width: 604px;
  min-height: 430px;
  height: auto;
  padding: 50px 50px 42px;
  display: flex;
  flex-direction: column;
  gap: 25px;
  border: 1px solid $color-cerulean-blue;
  border-radius: 16px;

  @include vp-1199 {
    max-width: 560px;
    min-height: 395px;
  }

  @include vp-767 {
    max-width: 100%;
    min-height: 435px;
    padding: 30px 14px 26px;
  }
}

.reviews__avatar img {
  border-radius: 50px;
  width: 90px;
  height: 90px;

  @include vp-767 {
    width: 60px;
    height: 60px;
  }
}

.reviews__author {
  display: flex;
  min-height: 90px;
  align-items: center;
  justify-content: space-between;

  @include vp-767 {
    min-height: 60px;
    gap: 13px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: unset;
  }
}

.reviews__avatar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;

  @include vp-767 {
    gap: 9px;
  }
}

.reviews__avatar p {
  max-width: 100px;
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  color: $color-cerulean-blue;

  @include vp-1199 {
    font-size: 20px;
    line-height: 24px;
  }

  @include vp-767 {
    padding-right: 7px;
    font-size: 14px;
    line-height: 18px;
  }
}

.reviews__data {
  display: flex;
  flex-wrap: nowrap;
  gap: 25px;

  @include vp-767 {
    width: 100%;
  }
}

.reviews__data dl {
  max-width: 300px;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;

  @include vp-767 {
    max-width: 150px;
  }
}

.reviews__data dt {
  font-size: 12px;
  line-height: 12px;
  color: $color-cerulean-blue;
  text-transform: uppercase;
}

.reviews__data dd {
  margin: 0;
  font-size: 14px;
  line-height: 14px;

  @include vp-767 {
    font-size: 12px;
  }
}

.reviews__text {
  display: flex;
  flex-direction: column;
  gap: 14px;

  @include vp-767 {
    gap: 13px;
  }
}

.reviews__text h3 {
  margin: 0;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  color: $color-cerulean-blue;

  @include vp-1199 {
    font-size: 20px;
    line-height: 24px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 18px;
  }
}

.reviews__text p {
  margin: 0;
  line-height: 26px;

  @include vp-1199 {
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 14px;
  }
}

.reviews__controls {
  width: 100%;
  max-width: 1270px;
  margin: 0 auto;
  padding: 15px 15px;
  position: relative;
  display: flex;
  order: 3;

  @include vp-1199 {
    padding: 0 45px;
    margin: 0;
  }

  @include vp-767 {
    position: static;
  }
}

.reviews__scrollbar {
  width: 100%;
  height: 2px;
  background-color: $overlay-photo;
  cursor: pointer;

  @include vp-767 {
    display: none;
  }
}

.swiper-scrollbar-drag {
  background-color: $color-cerulean-blue;
  border-radius: 0;
}

.reviews__buttons {
  padding-left: 52px;
  display: flex;
  position: absolute;
  right: 15px;
  top: 50%;
  gap: 10px;
  z-index: 2;
  background-color: $color-alabaster;
  transform: translate(0, -50%);

  @include vp-1199 {
    gap: 10px;
    right: 45px;
    padding-left: 64px;
  }

  @include vp-767 {
    gap: 8px;
    right: 15px;
    padding-left: 0;
    background-color: transparent;
    top: 33px;
    transform: none;
  }
}

.reviews__button {
  width: 22px;
  height: 22px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;

  &:hover {
    @include hover;
    background-color: $color-default-white;
  }

  &:focus {
    @include focus;
    background-color: $color-default-white;
  }

  &:active {
    @include active;
    background-color: $color-default-white;
  }

  &:active svg {
    @include active;
    opacity: 70%;
  }

  &:disabled {
    background-color: transparent;
    cursor: default;
  }

  &:disabled svg {
    color: $color-mischka;
  }
}

.reviews__button--prev svg {
  color: $color-cerulean-blue;
  width: 9px;
  height: 9px;
  transform: rotate(-135deg);
}

.reviews__button--next svg {
  width: 9px;
  height: 9px;
  color: $color-cerulean-blue;
  transform: rotate(45deg);
}
