.programs {
  position: relative;
  padding-bottom: 131px;

  @include vp-1199 {
    padding-bottom: 96px;
  }

  @include vp-767 {
    padding-bottom: 85px;
  }
}

.programs__wrapper {
  max-width: 1240px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  @include vp-1199 {
    max-width: 100%;
    margin: 0;
    padding: 0;
  }

  @include vp-767 {
    padding: 33px 0 0;
    background-color: transparent;
  }
}

.programs__title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  order: 1;

  @include vp-1199 {
    margin-bottom: 24px;
  }

  @include vp-767 {
    align-items: center;
    margin-bottom: 16px;
  }
}

.programs__link {
  padding-left: 28px;
  letter-spacing: 0.8px;
  background-color: transparent;

  @include vp-1199 {
    margin-top: 3px;
  }

  @include vp-767 {
    position: absolute;
    bottom: 67px;
    width: 203px;
  }
}

.programs__title h2 {
  margin: 0;
  padding-top: 4px;
  font-size: 50px;
  line-height: 50px;
  font-weight: 400;
  color: $color-cerulean-blue;

  @include vp-1199 {
    font-size: 34px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }
}

.programs__slider {
  order: 2;
  max-width: 1270px;
  margin: 0 auto 40px;
  padding: 0 15px;

  @include vp-1199 {
    max-width: 100%;
    margin: 0 0 25px;
    padding: 0 0 0 45px;
  }

  @include vp-767 {
    max-width: 100%;
    margin: 0 0 45px 0;
    padding: 0 15px;
  }
}

.programs__list {
  padding: 0;
  margin-bottom: 9px;
}

.programs__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 392px;
  background-color: $color-cerulean-active;
  border-radius: 16px;

  @include vp-1199 {
    max-width: 324px;
  }

  @include vp-767 {
    max-width: 100%;
    min-height: 400px;
  }
}

.programs__item picture {
  position: relative;
  object-fit: cover;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(49, 109, 194, 0.2);
    inset: 0;
    border-radius: 12px;
  }
}

.programs__item img {
  border-radius: 12px;

  @include vp-1199 {
    width: 100%;
  }
}

.programs__container {
  min-height: 120px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  position: absolute;

  @include vp-1199 {
    min-height: 115px;
  }

  @include vp-767 {
    padding: 18px 10px;
  }
}

.programs__container h3 {
  position: relative;
  min-width: 194px;
  padding: 9px 12px 7px 18px;
  margin: 0 0 -1px;
  border-radius: 12px 12px 0 0;
  width: fit-content;
  background-color: $color-default-white;
  text-align: start;
  font-size: 24px;
  line-height: 26px;
  color: $color-cerulean-blue;
  font-weight: 400;
  z-index: 3;

  &::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 18px;
    bottom: 0;
    right: -17px;
    background-image: url("../img/svg/angle.svg");
    transform: rotate(-90deg);
  }

  @include vp-1199 {
    min-width: 173px;
    padding: 9px 12px 6px 18px;
    font-size: 20px;
    line-height: 24px;
  }

  @include vp-767 {
    min-width: 146px;
    padding: 19px 12px 6px 18px;
    font-size: 16px;
    line-height: 18px;
  }
}

.programs__text {
  display: flex;
  padding: 5px 18px;
  background-color: $color-default-white;
  border-radius: 0 12px 12px 12px;
  gap: 16px;
  align-items: center;
  min-height: 79px;
  z-index: 3;

  @include vp-1199 {
    min-height: 77px;
  }

  @include vp-767 {
    min-height: 69px;
  }
}

.programs__text p {
  max-width: 239px;
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $color-charcoal;

  @include vp-1199 {
    max-width: 181px;
    line-height: 20px;
    letter-spacing: -0.4px;
  }

  @include vp-767 {
    max-width: 188px;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0;
  }
}

.programs__text a {
  display: flex;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: $color-cerulean-blue;
  justify-content: center;
  align-items: center;

  &:hover {
    @include hover;
    background-color: $color-default-white;
  }

  &:hover svg {
    @include hover;
    color: $color-cerulean-blue;
    transform: rotate(45deg);
    transition: $trans-default;
  }

  &:focus {
    @include focus;
    background-color: $color-default-white;
  }

  &:focus svg {
    @include focus;
    color: $color-cerulean-blue;
    transform: rotate(45deg);
    transition: $trans-default;
  }

  &:active {
    @include active;
    background-color: $color-default-white;
  }

  &:active svg {
    @include active;
    color: $color-cerulean-active;
    transform: rotate(45deg);
    transition: $trans-default;
  }

  & svg {
    color: $color-default-white;
  }
}

.programs__controls {
  width: 100%;
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
  order: 3;
  display: flex;

  @include vp-1199 {
    max-width: 100%;
    padding: 15px 45px;
    margin: 0;
  }

  @include vp-767 {
    padding: 0;
    position: static;
  }
}

.programs__scrollbar {
  width: 100%;
  height: 2px;
  background-color: $overlay-photo;
  cursor: pointer;

  @include vp-767 {
    display: none;
  }
}

.swiper-scrollbar-drag {
  background-color: $color-cerulean-blue;
  border-radius: 0;
}

.programs__buttons {
  padding-left: 52px;
  display: flex;
  position: absolute;
  right: 15px;
  top: 50%;
  gap: 10px;
  z-index: 2;
  background-color: $color-alabaster;
  transform: translate(0, -50%);

  @include vp-1199 {
    gap: 10px;
    right: 45px;
    padding-left: 64px;
  }

  @include vp-767 {
    gap: 8px;
    right: 15px;
    padding-left: 0;
    background-color: transparent;
    top: 33px;
    transform: none;
  }
}

.programs__button {
  width: 22px;
  height: 22px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;

  &:hover {
    @include hover;
    background-color: $color-default-white;
  }

  &:focus {
    @include focus;
    background-color: $color-default-white;
  }

  &:active {
    @include active;
    background-color: $color-default-white;
  }

  &:active svg {
    @include active;
    opacity: 70%;
  }

  &:disabled {
    background-color: transparent;
    cursor: default;
  }

  &:disabled svg {
    color: $color-mischka;
  }
}

.programs__button--prev svg {
  color: $color-cerulean-blue;
  width: 9px;
  height: 9px;
  transform: rotate(-135deg);
}

.programs__button--next svg {
  width: 9px;
  height: 9px;
  color: $color-cerulean-blue;
  transform: rotate(45deg);
}
