.feedback {
  padding: 120px 0 0;

  @include vp-1199 {
    padding: 100px 0 0;
  }

  @include vp-767 {
    padding: 67px 0 29px;
  }
}

.feedback__wrapper {
  max-width: 1250px;
  min-height: 803px;
  margin: 0 auto;
  position: relative;
  border-radius: 16px;

  @include vp-1199 {
    max-width: 100%;
    min-height: 780px;
    margin: 0;
  }

  @include vp-767 {
    min-height: 753px;
  }
}

.feedback__text {
  max-width: 498px;
  padding: 82px 74px 51px;
  display: flex;
  flex-direction: column;
  border-radius: 16px;
  background-color: $color-cerulean-blue;
  position: relative;
  z-index: 1;

  @include vp-1199 {
    max-width: 442px;
    padding: 60px 45px 58px;
    gap: 8px;
  }

  @include vp-767 {
    max-width: 100%;
    padding: 52px 15px 51px;
    gap: 16px;
  }
}

.feedback__wrapper picture {
  position: absolute;
  inset: 0;

  @include vp-767 {
    display: none;
  }
}

.feedback__wrapper img {
  height: 100%;
  border-radius: 16px;
  object-fit: cover;

  @include vp-1199 {
    width: 100%;
  }
}

.feedback__text h2 {
  margin: 0 0 25px;
  font-size: 32px;
  line-height: 36px;
  font-weight: 400;
  color: $color-default-white;

  @include vp-1199 {
    margin: 0 0 20px;
    font-size: 28px;
    line-height: 28px;
  }

  @include vp-767 {
    margin-bottom: 0;
    font-size: 18px;
    line-height: 22px;
  }
}

.feedback__form form {
  display: flex;
  flex-direction: column;
  gap: 18px 0;
  color: $color-default-white;

  @include vp-767 {
    gap: 20px 0;
  }
}

.feedback__text p {
  margin: 0 0 21px;
  line-height: 26px;
  color: $color-default-white;
  opacity: 0.9;

  @include vp-1199 {
    margin: 0 0 26px;
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.feedback__text label {
  color: $color-label;
  gap: 10px;

  @include vp-1199 {
    line-height: 20px;
    gap: 10px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 14px;
    gap: 12px;
  }
}

.feedback__text textarea {
  min-height: 120px;
  padding: 10px;
  background-color: transparent;
}

.feedback__text input,
.feedback__text textarea,
.feedback__text .form__select-button {
  background-color: transparent;
  border: 1px solid $color-border;
  border-radius: 8px;
  color: $color-default-white;

  &:not(:disabled):hover,
  &:not(:disabled):focus,
  &:not(:disabled):active {
    border-color: $color-default-white;
  }

  &:not(:disabled):focus-visible {
    outline: none;
  }

  &:disabled {
    background-color: rgba(255, 255, 255, 0.2);
    border-color: rgba(255, 255, 255, 0.4);
  }
}

.feedback__message.is-invalid textarea {
  border-color: #ff5e66;
  background-color: rgba(255, 94, 102, 0.2);
}

.feedback__form {
  & .form__select-label {
    color: rgba(240, 239, 233, 0.8);
    margin-bottom: 0;
  }

  & .form__select-text {
    color: $color-default-white;
  }

  & .form__select-icon {
    background-color: $color-default-white;
  }

  & textarea {
    min-height: 98px;
  }

  @include vp-1199 {
    gap: 20px 0;
  }

  @include vp-767 {
    max-width: 100%;
    padding: 0;

    & textarea {
      min-height: 94px;
    }
  }
}

.form__select-list {
  border: none;

  @include vp-767 {
    max-height: 150px;
  }
}

.custom-toggle--checkbox input[type="checkbox"]:disabled + .feedback__sort {
  background-color: rgba(255, 255, 255, 0.6);
  border: 1px solid $color-default-white;
}

.custom-toggle--checkbox input[type="checkbox"]:checked:disabled + .feedback__sort {
  background-color: $color-mischka;
  border: 1px solid $color-mischka;
}

.custom-toggle--checkbox input[type="checkbox"]:checked:disabled + .feedback__sort ::before {
  background-image: url("../img/svg/check-white.svg");
}

.custom-toggle--checkbox input[type="checkbox"]:checked + .feedback__sort {
  background-color: $color-default-white;
}

.custom-toggle--checkbox:hover input[type="checkbox"]:checked + .feedback__sort,
.custom-toggle--checkbox:focus input[type="checkbox"]:checked + .feedback__sort {
  background-color: $color-cerulean-blue;
  outline: 1px solid $color-default-white;
}

.custom-toggle--checkbox:hover input[type="checkbox"]:checked + .feedback__sort::before,
.custom-toggle--checkbox:focus input[type="checkbox"]:checked + .feedback__sort::before {
  background-image: url("../img/svg/check-white.svg");
}

.custom-toggle--checkbox input[type="checkbox"]:checked + .feedback__sort::before {
  background-image: url("../img/svg/check.svg");
}

.custom-toggle--checkbox-error {
  color: $color-wild-watermelon;
  font-size: 12px;
  line-height: 12px;
  margin-top: 5px;
}

.feedback__sort {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  background-color: $color-default-white;
  outline: 1px solid $color-transparent;
  top: 1px;
  left: 0;
  border-radius: 2px;

  @include vp-1199 {
    top: 8px;
  }
}

.feedback__button {
  border-color: $color-default-white;
  justify-content: center;
  color: $color-default-white;
  max-width: 100%;
  margin: 22px 0 0;
  background-color: transparent;

  &:not(:disabled):hover,
  &:not(:disabled):focus {
    background-color: $color-default-white;
    color: $color-cerulean-blue;
  }

  &:not(:disabled):hover::after,
  &:not(:disabled):focus::after {
    background-color: $color-cerulean-blue;
  }

  &:not(:disabled):hover svg,
  &:not(:disabled):focus svg {
    color: $color-default-white;
  }

  &:not(:disabled):active {
    background-color: $color-default-white;
    color: $color-cerulean-active;
  }

  &:not(:disabled):active::after {
    background-color: $color-cerulean-blue;
  }

  &:not(:disabled):active svg {
    color: $color-transparent;
  }

  &:disabled svg {
    color: rgba(255, 255, 255, 0.6);
  }

  &::after {
    background-color: $color-default-white;
  }

  & svg {
    color: $color-cerulean-blue;
  }

  @include vp-767 {
    margin: 9px 0 0;
    padding-left: 1px;
    padding-right: 39px;
  }
}
